.wrapper {
  background-color: #DFF0D8;
  border-radius: 5px;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
.checked {
  text-decoration: line-through;
}
.btn {
  display: none;
  width: 25px;
  height: 25px;
  border: 1px solid #749e65;
  border-radius: 0.25em;
  background-color: #749e65;
  color: white;
  margin-right: 5px;
  background-size: 50% 50%;
  cursor: pointer;
}

.wrapper:hover .btn{
  display: block;
}
.new-input {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.checkbox+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.checkbox+label::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  text-align: center;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #749e65;
  border-radius: 0.25em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  margin-left: 3px;
  cursor: pointer;
}
.checkbox:checked+label::before {
  border-color: #749e65;
  background-color: #749e65;
  content: "âœ”";
  color: white;
}

