.add-to-dos {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin: 20px 0;
}
.input {
  width: 100%;
  margin: 0 10px;
  border: 1px solid #749e65;
  border-radius: 5px;
}
.btn {
  background-color: #749e65;
  border-radius: 5px;
  border: 0;
  color: white;
  cursor: pointer;
}
